@import url(https://fonts.googleapis.com/css?family=Poppins:900i);
@import url(https://fonts.googleapis.com/css?family=Oswald:500);
body {
  margin: 0;
  padding: 0;
  margin-bottom: 30px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: linear-gradient(241deg, #000, #000d38);
  background-size: 600% 600%;

  -webkit-animation: AnimationName 15s ease infinite;
  animation: AnimationName 15s ease infinite;
}

@-webkit-keyframes AnimationName {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes AnimationName {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.bgParticle {
  position: absolute;
  width: 100%;
  height: 99%;
  z-index: -100;
}

h2,
h3,
h1 {
  color: white;
}

.button-style {
  font-family: "Poppins", sans-serif;
  border-radius: 0;
  color: #fff;
  text-transform: uppercase;
  max-width: 150px;
  font-size: 20px;
  margin: 0 auto;
  text-decoration: none;
  color: white;
  background: #6225e6;
  transition: 1s;
  box-shadow: 6px 6px 0 black;
  -webkit-transform: skewX(-15deg);
          transform: skewX(-15deg);
  margin-bottom: 10px;
}
.button-style-transition {
  font-family: "Poppins", sans-serif;
  border-radius: 0;
  color: #fff;
  padding: 6px 20px;
  text-transform: uppercase;
  max-width: 350px;
  font-size: 30px;
  margin: 0 auto;
  padding: 2px 15px;
  text-decoration: none;
  font-size: 25px;
  color: white;
  background: #6225e6;
  transition: 1s;
  box-shadow: 6px 6px 0 black;
  -webkit-transform: skewX(-15deg);
          transform: skewX(-15deg);
  margin-bottom: 10px;
}

.button-style:hover,
.button-style-transition:hover {
  background-color: #fff;
  border: 1px solid #fff;
  transition: 0.5s;
  color: #6225e6;
  box-shadow: 10px 10px 0 #fbc638;
}

.battle-deck .battleCard {
  transition: -webkit-transform 0.1s;
  transition: transform 0.1s;
  transition: transform 0.1s, -webkit-transform 0.1s;
  opacity: 0.5;
  cursor: pointer;
}

.battle-deck .battleCard.unUsed:hover {
  -webkit-transform: translate3d(0, -30px, 0);
          transform: translate3d(0, -30px, 0);
}

.unUsed {
  opacity: 1 !important;
}

.battleCardTitle {
  margin-bottom: 0.4rem;
  font-family: "Bangers";
}

.battleCardBody {
  padding: 0.9rem;
}

.help-section p:first-child {
  margin-top: 10px;
  color: #fff;
}
.help-section p {
  margin-bottom: 5px;
}

.help-section h3 {
  display: flex;
  margin-top: 25px;
}

.help-section h3 span {
  color: #000d38;
  display: block;
  border-radius: 20px;
  font-size: 0.9em;
  font-weight: bold;
  text-align: center;
  width: 30px;
  height: 30px;
  background-color: #fff;
  margin-right: 10px;
}

.help-section ul {
  padding: 0;
}

.help-section ul li {
  list-style: none;
  color: #fff;
}
.round-title-transition {
  font-size: 150px;
}

.nicknameTitle {
  font-size: 150px;
}
@media (min-width: 390px) {
  .responsive-card {
    padding: 0;
  }
}

.responsive-card :nth-child(2) {
  font-family: "Oswald";
}

@media (max-width: 667.98px) {
  .round-title-transition {
    font-size: 110px;
  }

  .nicknameTitle {
    font-size: 75px;
  }
}

.battleCard {
  border: 1px solid #161616;
  color: #fff;
  border: 1px solid #b6b6b659;
  background: radial-gradient(
      ellipse farthest-corner at right bottom,
      #dbdbdb 0%,
      #cfcfcf 8%,
      #bebebe 30%,
      #fff 75%,
      transparent 100%
    ),
    radial-gradient(
      ellipse farthest-corner at left top,
      #ffffff 0%,
      #f0f0f0 8%,
      #c7c7c7 25%,
      #fff 75%,
      #cfcfcf 100%
    );
}

.battleCard h5:before,
.battleCard h5:after {
  content: " \2022   ";
}

.ranking-table table {
  -webkit-transform: skew(-2deg);
          transform: skew(-2deg);
  width: 100%;
  border-collapse: collapse;
}

.ranking-table .table-head {
  background-color: #000;
  color: #fff;
  border-bottom: 0;
}

.ranking-table .table-head td {
  border: 0;
}

.ranking-table .table-head td:first-child {
  border-left: 0;
}

.ranking-table .table-head td:last-child {
  border-right: 0;
}

.ranking-table tr {
  padding: 0.5rem;
  background-color: #fff;
  background-image: url(/reims-0918-js-wild-battle/static/media/circleBackground.61c8ea6d.svg);
  background-repeat: repeat;
  background-blend-mode: multiply;
}

.ranking-table tr:first-child {
  background-image: none;
}

.ranking-table tr:nth-child(odd) {
  background-color: #ececec;
}

.ranking-table tr:nth-child(odd).table-head {
  background-color: #000;
  color: #fff;
  border: none;
}

.ranking-table td {
  min-width: 25%;
  text-align: center;
  border: 0;
}

.ranking-table th {
  padding: 15px;
}

.ranking-table td {
  font-family: "Oswald", cursive;
  font-size: 1.5rem;
}

.ranking-table td:nth-child(1) {
  font-family: "Bangers", cursive;
  font-size: 2rem;
}

.ranking-table p {
  font-family: "Bangers", cursive;
  font-size: 2rem;
  margin-bottom: 0;
}

.ranking-table p span {
  font-family: "Oswald", cursive;
  font-size: 2rem;
}

.ranking-table .winNumber {
  color: #28a745;
}

.ranking-table .loseNumber {
  color: #dc3545;
}

h2 {
  font-family: "Bangers", cursive;
  font-size: 3rem;
}

.historic-table .list-group-item {
  border: 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.125);
  background: none;
}

.historic-table .card {
  border: 3px solid black;
  padding: 1rem;
  background-color: #fff;
  box-shadow: 3px 3px 1px rgba(0, 0, 0, 0.5);
  -webkit-transform: skew(-5deg);
  transform: skew(-5deg);
  background-image: url(/reims-0918-js-wild-battle/static/media/circleBackground.61c8ea6d.svg);
  background-repeat: repeat;
  background-blend-mode: multiply;
}

.historic-table .list-group-item:nth-child(odd) .card {
  background-color: #ececec;
}

.historic-table .date {
  font-family: "Oswald";
  color: #333;
}

.historic-table .date:after {
  display: block;
  content: "";
  border-bottom: solid 3px #333;
  width: 10%;
  margin: 10px auto 0;
}

.historic-table .player {
  font-size: 2rem;
}

.historic-table .player span {
  color: #333;
}

.historic-table ul {
  background: none;
}

.historic-table .card div p {
  margin-bottom: 0;
}

.historic-table .winner {
  color: #28a745;
}

.historic-table .loser {
  color: #dc3545;
}
#statsSection {
  margin-bottom: 100px;
}

img.logo {
  margin-top: 20px;
  width: 180px;
}

footer a i {
  font-size: 16px;
}

footer a {
  text-decoration: none;
  color: grey;
}
footer a:hover {
  color: #fff;
  text-decoration: none;
}

footer {
  background-color: black;
}

.heroFrameAngle > a {
  -webkit-transform: skew(-5deg);
          transform: skew(-5deg);
  display: block;
  text-decoration: none;
  color: black;
}

#heroesListSection {
  color: #fff;
}

#heroesListSection h3 {
  font-family: "Bangers", cursive;
  text-align: right;
  font-size: 3rem;
}

#heroesListSection h2 {
  font-family: "Bangers", cursive;
  font-size: 4rem;
}

.cardTitleHero {
  font-family: "Bangers", cursive;
  text-align: center;
  font-size: 1.5vw;
  text-decoration: none;
  margin-bottom: 8px;
}

.card {
  font-family: "Bangers", cursive;
  color: #333;
}

#heroesListSection .card {
  overflow: hidden;
}

#heroesListSection .battleCard .card-body {
  padding-bottom: 0px;
}

#heroesListSection .heroFrameAngle .card-body {
  padding: 10px 0 0 0;
  z-index: 0;
  display: block;
  background-color: #fff;
}

#heroesListSection .list-heroes .card-img-top {
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}

#heroesListSection .list-heroes:hover .card-img-top {
  -webkit-transform: rotate(10deg) scale(1.4);
          transform: rotate(10deg) scale(1.4);
}

#heroesListSection .itemTitleBiography {
  font-weight: bold;
}

@media (max-width: 991.98px) {
  .cardTitleHero {
    font-size: 2.5vw;
  }
}

@media (max-width: 767.98px) {
