.collector,
#heroesListSection .heroFrameAngle .collector .card-body {
  color: #fff;
  border: 1px solid #ffea8959;
  text-shadow: 1px 0px 1px rgba(0, 0, 0, 0.8);
  background: radial-gradient(
      ellipse farthest-corner at right bottom,
      #fedb37 0%,
      #fdb931 8%,
      #c09434 30%,
      transparent 80%
    ),
    radial-gradient(
      ellipse farthest-corner at left top,
      #ffffff 0%,
      #ffffac 8%,
      #d1b464 25%,
      #91722b 100%
    );
}

.navHome a,
.navHome a:hover {
  color: black;
  min-height: 500px;
  text-decoration: none;
}

.navHome a .card {
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.2);
  transition: box-shadow 0.3s, opacity 0.3s;
  text-align: right;
  background: none;
  padding-top: 300px;
  margin-bottom: 50px;
  border: 3px solid #000;
  border-radius: 0;
  box-shadow: 0px 0px 0px 10px #fff;
  position: relative;
  overflow: hidden;
}

.navHome a .card:before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 110%;
  height: 110%;
  top: 0;
  left: 0;
  z-index: -1;
  transition: width 0.3s, height 0.3s, top 0.3s, left 0.3s, -webkit-transform 0.3s;
  transition: transform 0.3s, width 0.3s, height 0.3s, top 0.3s, left 0.3s;
  transition: transform 0.3s, width 0.3s, height 0.3s, top 0.3s, left 0.3s, -webkit-transform 0.3s;
}

.navHome a:hover .card:before {
  -webkit-transform: rotate(5deg);
  transform: rotate(5deg);
  width: 135%;
  height: 135%;
  top: -18%;
  left: -18%;
}

.navHome a .card.startfight:before {
  background: url(/reims-0918-js-wild-battle/static/media/fight.40f5cf89.png);
  background-size: cover;
  background-repeat: repeat;
  background-position: 50% 50%;
}

.navHome a .card.history:before {
  background: url(/reims-0918-js-wild-battle/static/media/stats.050f56dd.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.navHome a .card.list:before {
  background: url(/reims-0918-js-wild-battle/static/media/list.0d254127.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.navHome .card .card-body {
  border: 3px solid black;
  padding: 1rem;
  transition: padding-right 0.3s;
  background-color: #fff;
  box-shadow: 3px 3px 1px rgba(0, 0, 0, 0.5);
  -webkit-transform: skew(-5deg);
  transform: skew(-5deg);
}

.navHome a:hover .card .card-body {
  padding-right: 1.5rem;
}

.navHome a .card .card-body h5 {
  font-size: 1.9rem;
}

.navHome a .card .card-body h6 {
  font-size: 1.2rem;
}

.nicknameChecked {
  text-align: center;
  font-size: 3rem;
  transition-duration: 0.4s;
}

.pulsate-css {
  -webkit-animation: pulsate 1s ease-out;
          animation: pulsate 1s ease-out;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  opacity: 0;
  color: #fff;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  text-align: center;
}
.FontBangers {
  font-family: "Bangers", cursive;
  font-size: 9rem;
}
.fight {
  -webkit-animation: pulsate-fight 1s ease-out;
          animation: pulsate-fight 1s ease-out;
}
.fight span:hover {
  text-decoration: none;
  color: #007bff;
  cursor: pointer;
}

@-webkit-keyframes pulsate {
  0% {
    -webkit-transform: scale(0.1, 0.1);
            transform: scale(0.1, 0.1);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1.2, 1.2);
            transform: scale(1.2, 1.2);
    opacity: 0;
  }
}

@keyframes pulsate {
  0% {
    -webkit-transform: scale(0.1, 0.1);
            transform: scale(0.1, 0.1);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1.2, 1.2);
            transform: scale(1.2, 1.2);
    opacity: 0;
  }
}

@-webkit-keyframes pulsate-fight {
  0% {
    -webkit-transform: scale(0.1, 0.1);
            transform: scale(0.1, 0.1);
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes pulsate-fight {
  0% {
    -webkit-transform: scale(0.1, 0.1);
            transform: scale(0.1, 0.1);
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.btnNickname {
  transition: 0.5s;
  border-radius: 50%;
  color: green;
  cursor: pointer;
}
.btnNickname:hover {
  color: white;
  background-color: green;
}
.nickname-choice {
  font-size: 3rem;
  transition: 2s;
  text-align: center;
}

.win {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  top: 50%;
  font-size: 11vw;
  position: absolute;
  left: 28%;
  color: #28a745;
  text-shadow: 3px 3px #0f6e25;
}

.lose {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  top: 50%;
  font-size: 11vw;
  position: absolute;
  left: 24%;
  color: #dc3545;
  text-shadow: 3px 3px #8d121f;
}

@media (min-width: 992px) {
  .win {
    top: 46%;
    left: 24%;
  }

  .lose {
    top: 46%;
    left: 15%;
  }
}

